.youtube-cards-modal-section {
   
}
.youtube-modal-card {
    border-radius: getBorderRadius(5);
    background: getColor(medium-turquoise-100);
    display: flex;
    flex-direction: column;
    gap: getSpacing(5);
    padding: getSpacing(5) getSpacing(2);
    overflow: hidden;
    cursor: pointer;
    text-decoration: none;
    &__title {
        @include ps-typography(h3);
        color: getColor(mate-black-500);
    }
    &__description {
        @include ps-typography(body-1);
        color: getColor(black-900);
    }
    .modal-card__image {
        aspect-ratio: 16/9;
    }
}